/* counter*/
.product-block:hover .countdown {
  opacity: 0;
  -moz-opacity: 0;
  -khtml-opacity: 0;
  -webkit-opacity: 0; }

.countdown {
  opacity: 1;
  -moz-opacity: 1;
  -khtml-opacity: 1;
  -webkit-opacity: 1;
  left: 10px;
  padding: 0;
  position: absolute;
  right: 10px;
  text-align: center;
  bottom: 10px;
  vertical-align: top;
  transition: all 400ms ease-in-out 0s;
  -webkit-transition: all 400ms ease-in-out 0s;
  -moz-transition: all 400ms ease-in-out 0s;
  -o-transition: all 400ms ease-in-out 0s;
  -ms-transition: all 400ms ease-in-out 0s;
  top: auto; }

.countbox .timebox {
  margin: 0 4px 0 0;
  display: inline-block;
  position: relative;
  background: #fff;
  height: 40px;
  min-width: 43px;
  overflow: hidden;
  border-radius: 5px;
  text-align: center; }

.countbox .timebox:last-child::after {
  display: none; }

.countbox.hastime {
  width: auto;
  border-radius: 25px;
  padding: 2px 6px; }

.countbox .second .timebox-inner::after {
  display: none; }

.countbox .timebox .time {
  padding: 0;
